.main-container {
    background-color: black;
    height: 1000px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.animation {
    width: 100%;

    position: relative;
    display: flex;
    align-items: center;
    overflow: hidden;
    height: 60px;
    flex-shrink: 0;
    background: linear-gradient(90deg, #21ECC7 0.11%, #8672F3 99.91%);
}

.marquee-container {
    display: flex;
    white-space: nowrap;
    font-family: "PaytoneOne";
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    font-size: 24px;
    color: #fff;
    animation: marqueeAnimation 15s linear infinite;
    animation-delay: -5s;

}

@keyframes marqueeAnimation {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(-100%);
    }
}

.mainEng {
    margin-top: 90px;
    color: #FFF;
    font-family: Pretendard-ExtraBold;
    font-size: 30px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.mainKor {
    margin-top: 8px;
    color: #FFF;
    font-family: Pretendard-Light;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 150px;
}

.figure4 {
    height: 250px;
    width: 200px;
    background-image: url("/public/asset/figure4.png");
    background-position: left center;
    background-repeat: no-repeat;
    animation: moveUpDown 3s infinite;
    position: absolute;
    z-index: 0;
    left: 0;
    top: 100px;
}

.figure5 {
    height: 100px;
    width: 100px;
    background-image: url("/public/asset/figure5.png");
    background-position: left center;
    background-repeat: no-repeat;
    animation: moveUpDown 2s infinite;
    position: absolute;
    z-index: 0;
    right: 20px;
    top: 280px;
}

.figure6 {
    height: 200px;
    width: 100px;
    background-image: url("/public/asset/figure6.png");
    background-position: right center;
    background-repeat: no-repeat;
    animation: moveUpDown 5s infinite;
    position: absolute;
    z-index: 0;
    right: 0;
    top: 60px;
}

@keyframes moveUpDown {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-12px);
        /* 위로 13px 이동 */
    }

    100% {
        transform: translateY(0);
    }
}

.activity {
    z-index: 1;
    border: none;
    display: flex;
    align-items: center;
    width: 330px;
    margin: 0;
    margin-bottom: 14px;
    height: 80px;
    flex-shrink: 0;
    border-radius: 15px;
