.executive-container {
    background-color: black;
    height: 855px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.exeEng {
    margin-top: 90px;
    color: #FFF;
    font-family: Pretendard-ExtraBold;
    font-size: 30px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
}

.exeKor {
    margin-top: 8px;
    color: #FFF;
    font-family: Pretendard-ExtraLight;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 37px;
}

.executive-name {
    color: #FFF;
    font-family: Pretendard-ExtraLight;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-align: center;
    margin-bottom: 30px;
}

#Exbut {
    border: none;

    margin-top: 81px;
    margin-bottom: 81px;
    width: 330px;
    height: 45px;
    flex-shrink: 0;
    border-radius: 15px;
    background: #21ECC7;
    backdrop-filter: blur(2px);
}

#Exbut p {
    color: #000;
    font-family: Pretendard-ExtraBold;
    font-size: 16px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
}